body {
    font-family: "Urbanist", sans-serif;
    background-color: #1a191f;
    color: white;
  }
  
  h1 {
    margin: 0;
    padding: 0;
    text-align: left;
    color: white;
    font-family: "Urbanist", sans-serif;
    font-weight: 100;
    letter-spacing: 4px;
  }
  
  .header {
    margin: 50px 0 0;
  }
  
  .loading {
    opacity: 1;
    transition: opacity 0.5s ease;
    font-family: "Quicksand", sans-serif;
    font-weight: 100;
    color: white;
    position: absolute;
    display: flex;
    z-index: 9;
    align-items: center;
    width: 100%;
    height: 100%;
    justify-content: center;
  }
  
  #app {
    max-width: 90%;
    margin: 0 auto;
    position: relative;
  }
  
  .panorama-container,
  #photosphere-container {
    display: flex;
    position: relative;
    overflow: hidden;
    margin-top: 2em;
  }
  
  .panorama-container::before,
  #photosphere-container::before {
    display: flex;
    content: "";
    width: 100%;
    padding-top: 65%;
  }
  
  .panorama,
  #photosphere {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    transition: opacity 0.5s ease;
  }
  
  #photo-sphere {
    width: 100vw;
    height: 65vh;
  }
  
  .is-loaded {
    opacity: 1;
  }
  
  .is-loaded-txt {
    opacity: 0;
  }

  .caption {
    font-weight: 300;
    font-size: 14pt;
    flex: 1;
  }

  .specs {
    font-family: "Urbanist", sans-serif;
    font-weight: 100;
    font-size: 12pt;
    color: white;
    text-align: right;
    flex: 1;
  }

  .info {
      display: flex;
  }
  